body {
  background: #f1f1f1;
  color: #fff;
  font-family: 'Quicksand', sans-serif;
}

h1 {
  text-align: center;
}

.navbar-default {
  background-color: #2bc5da;
  min-height: 5.5em;
  border: 0;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
}

.calculator {
  margin: 8em auto 3em auto;
  height: 600px;
  width: 390px;
  background: #00838f;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  position: relative;
}

.calculator-output {
  background-color: #fff;
  height: 200px;
  width: 100%;
}

.calculator-body {
  height: 100%;
}

.calculator-body button {
  width: 100px;
  background: none;
  height: 100px;
  font-size: 32px;
  border: 0;
  padding: 0;
  -webkit-appearance: none;
}

.calculator-main {
  height: 400px;
  width: 300px;
  background-color: #0097a7;
}

.calculator-side-menu {
  background-color: #00bcd4;
  width: 100px;
  position: absolute;
  right: 0;
}

.calculator-side-menu button {
  font-size: 24px;
  height: 80px;
}

.calculator-main button:active,
.calculator-main button:hover,
.calculator-side-menu button:active,
.calculator-side-menu button:hover {
  background: #00838f;
}

footer {
  text-align: center;
}


